@import "src/scss/_vars.scss";
//
//
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
// =*= SECTION: fairy-tail =*=
// =*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=*=
//
.fairy-tail__inner {
    position: relative;
}

.fairy-tail__title span {
    margin-left: 180px;
}

//    slick-.
.fairy-tail__slider {
    width: 50%;

    //   .
    .slick-btn {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 5; //      .
        cursor: pointer;
        background-color: transparent;
        border: none;
        outline: none; //    ,     .
    }

    //  prev   .
    .slick-prev {
        left: 0;
    }

    //  next   .
    .slick-next {
        right: 0;
    }
}

//   .
.fairy-tail__container {
    max-width: 1230px;
    padding: 0 15px;
    margin: 0 auto;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .fairy-tail__body {
        width: 50%;
        margin-left: auto;
        padding-left: 55px;
        //      flex.
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        height: 100%;

        .fairy-tail__body-title {
            font-family: 'RobotoCondensed-Regular', serif;
        }

        .fairy-tail__text {
            padding: 10px 0;
        }
    }
}


